@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
#app {
  background-color: rgba(249, 250, 250, 0.938);
  min-height: 100vh;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

footer {
  position: absolute;
  bottom: 0;
  font-size: 1.2em;
  background-color: #4285f4;
  color: #fff;
  width: 99vw;
  text-align: center;
  padding: 5px;
}

.todo-item {
  font-size: 1.2em;
}

.date-time {
  font-size: 0.8em;
}

.delete-btn {
  cursor: pointer;
}

form {
  display: flex;
}


@media (min-width: 700px) {
  form {
    width: 40vw;
  }

  input {
    width: 90% !important;
  }
}

@media (max-width: 699px) {
  form {
    width: 85vw;
  }

  input {
    width: 80% !important;
  }
}

.collection-header {
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #4285f4 !important;
  color: #fff;
}
.todo-input-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 2.5rem;
}

.collection {
  overflow-wrap: break-word;
}

@media (min-width: 700px) {
  .collection {
    min-width: 40vw;
  }
}

@media (max-width: 699px) {
  .collection {
    width: 95vw;
  }
}

